
import './App.css';
import { WagmiConfig, createClient } from 'wagmi'
import { useAccount, useConnect, useDisconnect } from 'wagmi'
import { InjectedConnector } from 'wagmi/connectors/injected'

const client = createClient()

function App() {
  return (
    <WagmiConfig client={client}>
      <Profile />
    </WagmiConfig>
  );
}

function Profile() {
  const { data } = useAccount()
  const { connect } = useConnect({
    connector: new InjectedConnector(),
  })
  const { disconnect } = useDisconnect()

  if (data)
    return (
      <div className='addressNbutton'>
        Connectado a:  {data.address}
        <button className="button" onClick={() => disconnect()}>Desconectar</button>
      </div>
    )
  return <div className='addressNbutton'>
            <button className="button" onClick={() => connect()}>Connectar Wallet</button>
         </div>
}

export default App;
